/* You can add global styles to this file, and also import other style files */

/*================================================
Default CSS
=================================================*/
$main-font-family: "Open Sans", sans-serif;
$optional-font-family: "Catamaran", sans-serif;
$main-color: #086ad8;
$optional-color: #10dce8;
$white-color: #ffffff;
$black-color: #212121;
$paragraph-color: #666666;
$font-size: 15px;
$transition: 0.5s;

body {
  padding: 0;
  margin: 0;
  font: {
    family: $main-font-family;
    size: $font-size;
  }
}
a {
  text-decoration: none;
  transition: $transition;
  color: $black-color;

  &:hover,
  &:focus {
    color: $main-color;
    text-decoration: none;
  }
}
button {
  outline: 0 !important;
}
p {
  margin-bottom: 15px;
  line-height: 1.8;
  color: $paragraph-color;
  font: {
    family: $main-font-family;
    size: $font-size;
    weight: 400;
  }
  &:last-child {
    margin-bottom: 0;
  }
}
i {
  line-height: normal;
}
.d-table {
  width: 100%;
  height: 100%;

  &-cell {
    vertical-align: middle;
  }
}
img {
  max-width: 100%;
  height: auto;
}
.ptb-100 {
  padding: {
    top: 100px;
    bottom: 100px;
  }
}
.pt-100 {
  padding-top: 100px;
}
.pb-100 {
  padding-bottom: 100px;
}
.pt-70 {
  padding-top: 70px;
}
.pb-70 {
  padding-bottom: 70px;
}
.ptb-50 {
  padding: {
    top: 50px;
    bottom: 50px;
  }
}
.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: $black-color;
  text-transform: capitalize;
  font: {
    family: $optional-font-family;
    weight: bold;
  }
  &:last-child {
    margin-bottom: 0;
  }
}
.default-btn {
  display: inline-block;
  padding: 12px 35px 13px;
  background-color: $main-color;
  color: $white-color;
  border-radius: 2px;
  font: {
    size: $font-size;
    weight: 600;
  }
  &:hover {
    background-color: $optional-color;
    color: $white-color;
  }
}
.optional-btn {
  display: inline-block;
  padding: 13px 35px 14px;
  background-color: $optional-color;
  color: $white-color;
  border-radius: 2px;
  margin-left: 16px;
  font: {
    size: $font-size;
    weight: 500;
  }
  &:hover {
    background-color: $main-color;
    color: $white-color;
  }
}
.section-title {
  text-align: center;
  margin: {
    top: -5px;
    bottom: 60px;
  }
  h2 {
    font-size: 40px;
    margin-bottom: 12px;
    position: relative;
    line-height: 1;
  }
  span {
    color: $main-color;
  }
  p {
    max-width: 600px;
    margin: auto;
  }
  .bar {
    height: 4px;
    width: 85px;
    background: $main-color;
    margin: 20px auto 0;
    position: relative;
    border-radius: 5px;
    overflow: hidden;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 5px;
      background: $white-color;
      animation: {
        duration: 2s;
        timing-function: linear;
        iteration-count: infinite;
        name: MOVE-BG;
      }
    }
  }
}
@-webkit-keyframes MOVE-BG {
  from {
    -webkit-transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(85px);
  }
}
@keyframes MOVE-BG {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(85px);
  }
}
.default-shape {
  .shape-1 {
    position: absolute;
    right: 10%;
    top: 16%;
    z-index: -1;
    animation: animationFramesOne 20s infinite linear;
  }
  .shape-2 {
    position: absolute;
    left: 10%;
    top: 16%;
    z-index: -1;

    img {
      animation-name: rotateMe;
      animation-duration: 10s;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
    }
  }
  .shape-3 {
    position: absolute;
    left: 5%;
    bottom: 25%;
    z-index: -1;
    animation: animationFramesOne 20s infinite linear;
  }
  .shape-4 {
    position: absolute;
    right: 10%;
    bottom: 25%;
    z-index: -1;
    animation: animationFramesOne 20s infinite linear;
  }
  .shape-5 {
    position: absolute;
    left: 50%;
    top: 10%;
    z-index: -1;
    animation: animationFramesOne 20s infinite linear;
  }
}

/*================================================
Preloader Area CSS
=================================================*/
.preloader-area {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $main-color;
  z-index: 999999;

  .preloader {
    width: 80px;
    height: 80px;
    display: inline-block;
    padding: 0px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);

    span {
      position: absolute;
      display: inline-block;
      width: 80px;
      height: 80px;
      border-radius: 100%;
      background-color: $white-color;
      -webkit-animation: preloader 1.3s linear infinite;
      animation: preloader 1.3s linear infinite;

      &:last-child {
        animation-delay: -0.8s;
        -webkit-animation-delay: -0.8s;
      }
    }
  }
}
@keyframes preloader {
  0% {
    transform: scale(0, 0);
    opacity: 0.5;
  }
  100% {
    transform: scale(1, 1);
    opacity: 0;
  }
}
@-webkit-keyframes preloader {
  0% {
    -webkit-transform: scale(0, 0);
    opacity: 0.5;
  }
  100% {
    -webkit-transform: scale(1, 1);
    opacity: 0;
  }
}

/*================================================
Navbar Area CSS
=================================================*/
.navbar-area {
  .navbar {
    transition: $transition;
    padding: {
      right: 0;
      top: 0;
      left: 0;
      bottom: 0;
    }
    ul {
      padding-left: 0;
      list-style-type: none;
      margin-bottom: 0;
    }
    .navbar-nav {
      margin: {
        left: auto;
      }
      .nav-item {
        position: relative;
        padding: 0;

        a {
          color: $black-color;
          text-transform: capitalize;
          font: {
            size: 15.5px;
            weight: 600;
          }
          padding: {
            left: 0;
            right: 0;
            top: 25px;
            bottom: 25px;
          }
          margin: {
            left: 14px;
            right: 14px;
          }
          &:hover,
          &:focus,
          &.active {
            color: $main-color;
          }
          i {
            font-size: 16px;
            position: relative;
            top: 2px;
            display: inline-block;
            margin: {
              left: -2px;
              right: -4px;
            }
          }
        }
        &:last-child {
          a {
            margin-right: 0;
          }
        }
        &:first-child {
          a {
            margin-left: 0;
          }
        }
        &:hover,
        &.active {
          a {
            color: $main-color;
            transition: $transition;
          }
        }
        .dropdown-menu {
          box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
          background: $white-color;
          position: absolute;
          border: none;
          top: 80px;
          left: 0;
          width: 250px;
          z-index: 99;
          display: block;
          opacity: 0;
          visibility: hidden;
          border-radius: 0;
          transition: all 0.3s ease-in-out;
          margin-top: 20px;
          padding: {
            top: 20px;
            left: 5px;
            right: 5px;
            bottom: 20px;
          }
          li {
            padding: {
              left: 0;
              right: 0;
              top: 0;
              bottom: 0;
            }
            a {
              text-transform: capitalize;
              padding: 8px 15px;
              margin: 0;
              position: relative;
              color: $black-color;
              font: {
                size: 14.5px;
                weight: 600;
              }
              &:hover,
              &:focus,
              &.active {
                color: $main-color;
                letter-spacing: 1px;
                &::before {
                  display: none;
                }
              }
            }
            .dropdown-menu {
              left: -255px;
              top: 0;
              opacity: 0;
              visibility: hidden;

              li {
                a {
                  color: $black-color;

                  &:hover,
                  &:focus,
                  &.active {
                    color: $main-color;
                  }
                }
                .dropdown-menu {
                  left: 220px;
                  top: 0;
                  opacity: 0;
                  visibility: hidden;

                  li {
                    a {
                      color: $black-color;

                      &:hover,
                      &:focus,
                      &.active {
                        color: $main-color;
                      }
                    }
                    .dropdown-menu {
                      left: -250px;
                      top: 0;
                      opacity: 0;
                      visibility: hidden;

                      li {
                        a {
                          color: $black-color;

                          &:hover,
                          &:focus,
                          &.active {
                            color: $main-color;
                          }
                        }
                        .dropdown-menu {
                          left: -250px;
                          top: 0;
                          opacity: 0;
                          visibility: hidden;

                          li {
                            a {
                              color: $black-color;

                              &:hover,
                              &:focus,
                              &.active {
                                color: $main-color;
                              }
                            }
                            .dropdown-menu {
                              left: -250px;
                              top: 0;
                              opacity: 0;
                              visibility: hidden;

                              li {
                                a {
                                  color: $black-color;

                                  &:hover,
                                  &:focus,
                                  &.active {
                                    color: $main-color;
                                  }
                                }
                                .dropdown-menu {
                                  left: -250px;
                                  top: 0;
                                  opacity: 0;
                                  visibility: hidden;

                                  li {
                                    a {
                                      color: $black-color;

                                      &:hover,
                                      &:focus,
                                      &.active {
                                        color: $main-color;
                                      }
                                    }
                                  }
                                }
                                &.active {
                                  a {
                                    color: $main-color;
                                  }
                                }
                                &:hover {
                                  .dropdown-menu {
                                    opacity: 1;
                                    visibility: visible;
                                    top: -15px;
                                  }
                                }
                              }
                            }
                            &.active {
                              a {
                                color: $main-color;
                              }
                            }
                            &:hover {
                              .dropdown-menu {
                                opacity: 1;
                                visibility: visible;
                                top: -15px;
                              }
                            }
                          }
                        }
                        &.active {
                          a {
                            color: $main-color;
                          }
                        }
                        &:hover {
                          .dropdown-menu {
                            opacity: 1;
                            visibility: visible;
                            top: -15px;
                          }
                        }
                      }
                    }
                    &.active {
                      a {
                        color: $main-color;
                      }
                    }
                    &:hover {
                      .dropdown-menu {
                        opacity: 1;
                        visibility: visible;
                        top: -15px;
                      }
                    }
                  }
                }
                &.active {
                  a {
                    color: $main-color;
                  }
                }
                &:hover {
                  .dropdown-menu {
                    opacity: 1;
                    visibility: visible;
                    top: -15px;
                  }
                }
              }
            }
            &.active {
              a {
                color: $main-color;
              }
            }
            &:hover {
              .dropdown-menu {
                opacity: 1;
                visibility: visible;
                top: -15px;
              }
            }
          }
        }
        &:hover {
          .dropdown-menu {
            opacity: 1;
            visibility: visible;
            top: 100%;
            margin-top: 0;
          }
        }
      }
    }
    .others-options {
      margin-left: auto;

      .option-item {
        color: $black-color;
        display: inline-block;
        position: relative;
        line-height: 1;

        .search-btn {
          cursor: pointer;
          transition: $transition;
          color: $black-color;
          font-size: 20px;
          font-weight: 600;
        }
        .close-btn {
          cursor: pointer;
          display: none;
          transition: $transition;
          width: 20px;
          color: $black-color;
          font-size: 18px;

          &.active {
            display: block;
          }
        }
      }
      .burger-menu {
        width: 35px;
        height: auto;
        background-color: transparent;
        margin-left: 20px;
        cursor: pointer;
        display: inline-block;
        position: relative;
        top: 1px;

        span {
          display: block;
          width: 35px;
          height: 3px;
          background: $black-color;
          transition: $transition;
          border-radius: 30px;
          margin: {
            left: auto;
            bottom: 4px;
          }
          &:nth-child(3) {
            width: 28px;
            margin: {
              left: auto;
              bottom: 0;
            }
          }
        }
        &:hover {
          span {
            background: $main-color;

            &:nth-child(3) {
              width: 35px;
            }
          }
        }
      }
      .default-btn {
        margin-left: 20px;
      }
    }
  }
  &.is-sticky {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 999;
    box-shadow: 0 2px 28px 0 rgba(0, 0, 0, 0.09);
    background-color: $white-color !important;
    animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
  }
  &.navbar-style-two {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: auto;
    z-index: 999;

    &.is-sticky {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 999;
      box-shadow: 0 2px 28px 0 rgba(0, 0, 0, 0.09);
      background-color: $white-color !important;
      animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
    }
    &.bg-color {
      background-color: rgba(255, 255, 255, 0.5);
    }
  }
}
.search-overlay {
  display: none;

  &.search-popup {
    position: absolute;
    top: 100%;
    width: 300px;
    right: 0;
    background: $white-color;
    z-index: 2;
    padding: 20px;
    box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
    margin-top: 23px;

    .search-form {
      position: relative;

      .search-input {
        display: block;
        width: 100%;
        height: 50px;
        line-height: initial;
        border: 1px solid #eeeeee;
        color: $black-color;
        outline: 0;
        transition: $transition;
        padding: {
          top: 4px;
          left: 10px;
        }
        &:focus {
          border-color: $main-color;
        }
      }
      .search-button {
        position: absolute;
        right: 0;
        top: 0;
        height: 50px;
        background: transparent;
        border: none;
        width: 50px;
        outline: 0;
        color: $paragraph-color;
        transition: $transition;
        padding: 0;

        &:hover,
        &:focus {
          color: $main-color;
        }
      }
    }
  }
}
.sidebar-modal {
  position: fixed;
  right: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  opacity: 0;
  visibility: hidden;
  transition: $transition;
  overflow: hidden;

  .sidebar-modal-inner {
    position: absolute;
    right: -100%;
    top: 0;
    width: 30%;
    overflow-y: scroll;
    height: 100%;
    background-color: $white-color;
    transition: 0.7s;
    z-index: 1;
    padding: {
      top: 80px;
      bottom: 40px;
      left: 40px;
      right: 40px;
    }
    .close-btn {
      display: inline-block;
      position: absolute;
      right: 35px;
      top: 20px;
      font-size: 20px;
      transition: $transition;
      color: $black-color;
      opacity: 0.66;
      cursor: pointer;

      &:hover {
        opacity: 1;
      }
    }
  }
  .sidebar-about-area {
    margin: {
      bottom: 40px;
    }
    .title {
      h2 {
        margin-bottom: 0;
        font: {
          size: 24px;
          weight: 700;
        }
        font-family: $main-font-family;
      }
      p {
        margin-top: 15px;
        font-size: 15px;
      }
    }
  }
  .sidebar-contact-feed {
    margin-bottom: 40px;

    h2 {
      margin-bottom: 25px;
      font: {
        size: 22px;
      }
    }
    .contact-form {
      padding: 0;
      box-shadow: unset;

      form {
        .form-group {
          margin-bottom: 15px;
        }
        .form-control {
          height: 50px;
          padding: 0 15px;
          line-height: initial;
          color: $black-color;
          background-color: transparent;
          border: 1px solid #f3f3f3;
          border-radius: 5px;
          transition: $transition;
          box-shadow: unset !important;
          font: {
            size: 14px;
            weight: 400;
          }
          &:focus {
            border-color: $main-color;
          }
          &::placeholder {
            color: #666666;
          }
        }
        textarea.form-control {
          height: auto !important;
          padding-top: 15px;
        }
      }
      .send-btn {
        margin-top: 10px;
      }
      .send-btn {
        .send-btn-one {
          display: inline-block;
          padding: 14px 35px;
          background-color: $main-color;
          color: $white-color;
          border-radius: 2px;
          transition: $transition;
          border: none;
          font: {
            size: $font-size;
            weight: 600;
          }
          &:hover {
            background-color: $optional-color;
            color: $white-color;
          }
        }
      }
    }
  }
  .sidebar-contact-area {
    .contact-info {
      text-align: center;

      .contact-info-content {
        h2 {
          margin-bottom: 0;
          text-transform: lowercase;
          font: {
            size: 20px;
            family: $main-font-family;
          }
          a {
            display: inline-block;
            color: $main-color;

            &:hover {
              color: $black-color;
            }
            &:not(:first-child) {
              color: $black-color;

              &:hover {
                color: $main-color;
              }
            }
          }
          span {
            display: block;
            color: $paragraph-color;
            margin: {
              top: 15px;
              bottom: 10px;
            }
            font: {
              size: 14px;
              weight: 600;
            }
          }
        }
        .social {
          padding-left: 0;
          list-style-type: none;
          margin: {
            bottom: 0;
            top: 20px;
          }
          li {
            display: inline-block;
            margin: 0 4px;
            padding-left: 0;

            a {
              width: 35px;
              height: 35px;
              line-height: 32px;
              border: 1px solid #dadada;
              border-radius: 50%;
              color: #aba5a5;
              display: block;

              &:hover {
                color: $white-color;
                border-color: $main-color;
                background-color: $main-color;
              }
            }
          }
        }
      }
    }
  }
  &.active {
    opacity: 1;
    visibility: visible;

    .sidebar-modal-inner {
      right: 0;
    }
  }
}
.navbar-area {
  .navbar-light {
    .navbar-toggler-icon {
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(0, 0, 0, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E");
    }
  }
}
// Mobile and iPad Navbar
@media only screen and (max-width: 767px) {
  .navbar-area {
    padding: {
      top: 15px;
      bottom: 15px;
    }
    .navbar-light {
      .navbar-toggler {
        border-color: $black-color;
        border-radius: 0;
      }
    }
    .collapse:not(.show).active {
      display: block;
    }
    .navbar {
      .navbar-nav {
        margin: 15px 0 0;
        background-color: $main-color;
        padding: 5px 15px;
        height: 350px;
        overflow-y: scroll;

        .nav-item {
          padding-top: 10px;
          padding-bottom: 10px;

          a {
            font-size: 15px;
            padding-left: 0;
            padding-right: 0;
            padding-top: 0;
            color: $white-color !important;
            padding-bottom: 0;
            margin-left: 0;
            margin-right: 0;
          }
          .dropdown-menu {
            box-shadow: unset;
            position: relative;
            background-color: transparent !important;
            border: none;
            top: 0;
            left: 0;
            margin-top: 0 !important;
            width: 100%;
            z-index: 99;
            display: block;
            opacity: 1;
            visibility: visible;
            padding: 5px 20px 0;

            li {
              a {
                font-size: 14px;
                padding-left: 0 !important;
              }
              .dropdown-menu {
                left: 0;
                top: 0;
                opacity: 1;
                visibility: visible;
              }
            }
          }
          &:hover {
            .dropdown-menu {
              top: 0;
            }
          }
        }
      }
      .others-options {
        display: none;
      }
    }
  }
}

/*================================================
Banner Area CSS
=================================================*/
.main-banner-area {
  position: relative;
  overflow: hidden;
}
.home-sliders {
  .home-item {
    position: relative;
    height: 850px;
    z-index: 1;
    background: {
      position: center;
      size: cover;
      repeat: no-repeat;
    }
    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      z-index: -1;
      width: 100%;
      height: 100%;
      background: $main-color;
      opacity: 0.8;
    }
    &.item-bg1 {
      background-image: url(assets/img/home-one/slider-bg.jpg);
    }
    &.item-bg2 {
      background-image: url(assets/img/home-one/slider-bg.jpg);
    }
    &.item-bg3 {
      background-image: url(assets/img/home-one/slider-bg.jpg);
    }
    .main-banner-content {
      position: relative;
      max-width: 630px;

      h1 {
        font-size: 70px;
        color: $white-color;
        margin: 5px 0 15px 0;
      }
      p {
        color: $white-color;
        margin-bottom: 0;
        max-width: 600px;
