  }
  &.owl-theme {
    .owl-nav {
      margin-top: 0;
      opacity: 0;
      visibility: hidden;
      transition: $transition;

      [class*="owl-"] {
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translateY(-50%);
        color: $white-color;
        font-size: 25px;
        margin: 0;
        padding: 0;
        background: $black-color;
        border-radius: 2px;
        width: 55px;
        height: 55px;
        transition: $transition;
        border: none;

        i {
          position: absolute;
          left: 0;
          right: 0;
          top: 50%;
          transform: translateY(-50%);
        }
        &.owl-next {
          left: auto;
          right: 20px;
        }
        &:hover {
          background-color: $main-color;
          color: $white-color;
        }
      }
    }
    .owl-dots {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 25px;

      .owl-dot {
        span {
          width: 13px;
          height: 13px;
          margin: 0 5px;
          background: $white-color;
          transition: $transition;
          border-radius: 50%;
          transform: scale(0.9);
        }
        &:hover,
        &.active {
          span {
            background-color: $optional-color;
          }
        }
        &.active {
          span {
            transform: scale(1.2);
          }
        }
      }
    }
    &:hover {
      .owl-nav {
        opacity: 1;
        visibility: visible;
      }
    }
  }
}
@keyframes movescale {
