blockquote,
.blockquote {
  overflow: hidden;
  background-color: #fafafa;
  padding: 50px !important;
  position: relative;
  text-align: center;
  z-index: 1;
  margin: {
    bottom: 20px;
    top: 20px;
  }
  p {
    color: $black-color;
    line-height: 1.6;
    margin-bottom: 0;
    font: {
      style: italic;
      weight: 700;
      size: 22px !important;
    }
  }
  cite {
    display: none;
  }
  &::after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 3px;
    background-color: $main-color;
    margin: {
      top: 20px;
      bottom: 20px;
    }
  }
}
.post-navigation {
  border: {
    top: 1px solid #eeeeee;
    bottom: 1px solid #eeeeee;
  }
  padding: {
    top: 20px;
    bottom: 20px;
  }
  .navigation-links {
    display: flex;
    flex-wrap: wrap;

    .nav-previous {
      flex: 0 0 50%;
      max-width: 50%;

      a {
        display: inline-block;
        font-weight: 600;

        i {
          margin-right: 2px;
          transition: $transition;
        }
        &:hover {
          i {
            margin-right: 0;
          }
        }
      }
    }
    .nav-next {
      flex: 0 0 50%;
      max-width: 50%;
      text-align: right;

      a {
        display: inline-block;
        font-weight: 600;

        i {
          margin-left: 2px;
          transition: $transition;
        }
        &:hover {
          i {
            margin-left: 0;
          }
        }
      }
    }
    div {
      a {
        display: inline-block;
        font: {
          weight: 400;
        }
      }
    }
  }
}
